<script>
  import Button, { Label } from "@smui/button";
  import { navigate } from "svelte-routing";

  import { Input } from "@smui/textfield";
  import Paper from "@smui/paper";

  import BaraCard, { Head, Body } from "~/components/bara-card";
  import { get_userId_from_jwt, get_userInfo_from_id } from "~/utils/account";
  import { request } from "~/utils/request";

  export let location;

  let email = "";
  let password = "";
  let is_login = "";

  const login = (e) => {
    e.preventDefault();
    request(
      "/api/user/login",
      "post",
      { accept: "application/json", "Content-Type": "application/json" },
      JSON.stringify({
        email: email,
        password: password,
      })
    ).then(({ status, data }) => {
      console.log(status);
      console.log(data);
      if (status == 200) {
        // 요청이 성공적으로 처리되었을 경우
        alert("로그인 성공!");
        localStorage.setItem("access_token", data.access_token);
        localStorage.setItem("refresh_token", data.refresh_token);
        is_login = true;

        navigate("/dashboard", { replace: true });
      } else if (status == 402) {
        // 요청 파라미터의 Type은 유효하나 함수 내부에서 Validation이 실패할 경우
        alert(data.message);
      } else if (status == 422) {
        // 요청 파라미터가 유효하지 않은 경우
        alert("요청이 잘못되었습니다.");
      }
    });
  };
</script>

<div class="card-wrap">
  <BaraCard
    style="position:relative; 
    top:50%; 
    left:50%; 
    transform:translate(-50%, -50%); 
    width:300px;
    padding:0px 20px;
    background-color:#313a46;
    "
  >
    <Body>
      <form id="container">
        <div class="title-wrap">
          <img src="static/images/bara_logo_hrz.svg" alt="logo" />
        </div>
        <div class="input-wrap">
          <Paper class="input-box" variant="unelevated">
            <Input
              bind:value={email}
              style=""
              placeholder="Search"
              class="solo-input"
            />
          </Paper>
          <Paper class="input-box" variant="unelevated">
            <Input
              bind:value={password}
              style=""
              placeholder="Search"
              class="solo-input"
            />
          </Paper>
        </div>

        <div class="action-wrap" on:click={login}>
          <Button
            variant="unelevated"
            style=" background-color:#727cf5; box-shadow:0 2px 6px 0 rgb(114 124 245
          / 50%); "
          >
            <Label>등록</Label>
          </Button>
        </div>
      </form>
    </Body>
  </BaraCard>
</div>

