<script>
  import DataTable, { Head, Body, Row, Cell } from "@smui/data-table";
  import IconButton from "@smui/icon-button";
  import Dialog, { Title, Content, Actions } from "@smui/dialog";
  import Button, { Label } from "@smui/button";

  let open = false;
  let items = [];
  let delete_id;
  function openModal(id) {
    open = true;
    delete_id = id;
  }
  fetch("/api/user", {
    method: "get",
    headers: {
      accept: "application/json",
      Authorization: `Bearer ${localStorage.getItem("access_token")}`,
    },
  })
    .then((response) => response.json())
    .then((data) => {
      if (data.success) {
        items = data.result;
      }
    });
</script>

<DataTable table$aria-label="People list" style="width: 100%;">
  <Head>
    <Row>
      <Cell columnId="id">아이디</Cell>
      <Cell columnId="email">이메일</Cell>
      <Cell columnId="role_name">직급</Cell>
      <Cell columnId="name">이름</Cell>
      <Cell>탈퇴</Cell>
    </Row>
  </Head>
  <Body>
    {#each items as item}
      <Row>
        <Cell>{item.id}</Cell>
        <Cell>{item.email}</Cell>
        <Cell>{item.role_name}</Cell>
        <Cell>{item.name}</Cell>
        <Cell
          ><IconButton
            class="material-icons"
            on:click={() => {
              openModal(item.id);
            }}>delete_outline</IconButton
          ></Cell
        >
      </Row>
    {/each}
  </Body>
</DataTable>

<Dialog
  bind:open
  aria-labelledby="simple-title"
  aria-describedby="simple-content"
>
  <!-- Title cannot contain leading whitespace due to mdc-typography-baseline-top() -->
  <Title id="simple-title">삭제</Title>
  <Content id="simple-content">{delete_id}를 삭제하시겠습니까?</Content>
  <Actions>
    <Button>
      <Label>No</Label>
    </Button>
    <Button>
      <Label>Yes</Label>
    </Button>
  </Actions>
</Dialog>
